import { Injectable } from '@angular/core';
import { select, NgRedux } from '@angular-redux/store';
import { Observable } from 'rxjs';
import { NewsApiDataApiActions } from './api/news-api.actions';
import { News, NewsQuery, SDGScores } from './api/news-api.model';
import { AppState } from '../../store/model';

@Injectable({
    providedIn: 'root'
})
export class NewsService {
    @select(['newsDataset', 'data', 'news'])
    public news$: Observable<News[]>;

    @select(['newsDataset', 'data', 'sdgScores'])
    public sdgScores$: Observable<SDGScores[]>;

    constructor(private newsActions: NewsApiDataApiActions, private store: NgRedux<AppState>) {}

    public loadDataset(newsQuery: NewsQuery) {
        this.newsActions.loadData(newsQuery); // store
        this.newsActions.loadNews(newsQuery); // 
        this.newsActions.loadedData(); // storehasDatasettrue
    }

    public loadSDGScores(newsQuery: NewsQuery) {
        this.newsActions.loadSDGScores(newsQuery); // SDG
    }

    // 
    public saveLastFilter(lastFilter: NewsQuery) {
        this.newsActions.saveLastFilter(lastFilter);
    }

    // 
    public getLastFilter(): NewsQuery {
        return this.store.getState()['newsDataset'].lastFilter;
    }

    // storedata
    public hasDataset() {
        return this.store.getState()['newsDataset'].hasDataset;
    }
}
